.prices-services {
  &__title {
    margin: 0;
  }
  &__table {
    display: none;
  }
  &__list {
    gap: 1rem;
    padding-left: 0;
    padding-bottom: 1em;
  }
  &__slider {
    background-color: white;
    padding: 1em;
    padding-top: 0;
    border: $border;
  }
  &__slider button {
    cursor: pointer;
    border: $border;
    padding: 0.5em 1em;
    background-color: #ff3e79;
    color: white;
    display: inline-block;
    border-radius: 16px;
  }
  &__slider dl {
    display: inline;
    white-space: nowrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
  }
  &__slider dl dt {
    color: $text-gray;
    opacity: 0.72;
    padding-top: 1em;
  }
  &__slider dl dd {
    margin: 0;
    color: $text;
  }
  &__slider button svg {
    -ms-flex-item-align: end;
    -ms-grid-row-align: end;
    align-self: end;
    margin-left: auto;
  }
  &__expand {
    cursor: pointer;
  }
  &__hidden.hidden {
    display: none;
    cursor: pointer;
  }
}

.commercial-offer {
  a {
    text-decoration: none;
    color: $text;
    font-family: "DemiBold", sans-serif;
    font-weight: 600;
  }
}
@media (min-width: 768px) {
  .prices-services__list {
    display: none;
  }
  .prices-services__block {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    min-width: 100%;
    margin-right: 1em;
  }
  .prices-services__table {
      width: 100%;
      margin: 1em 1em 1em 0;
      display: block;
      border-collapse: collapse;
    & thead tr th {
      padding: 1em;
      text-align: start;
    }
    & tbody tr td {
      padding: 1em;
      background: white;
      width: 33%;
      border-bottom: $border;
      button svg {
        padding-left: 1em;
      }
    }
    & button {
      border: $border;
      padding: 0.5em 1.5em;
      background-color: #ff3e79;
      color: white;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      border-radius: 16px;
    }
    .swiper {
      display: none;
    }
  }
}

